h1, h2, h3 {
  margin: 0 0 12px;
}

label {
  margin-top: 12px;
  font-weight: 600;
  display: block;
}

input, select {
  width: 100%;
  margin-top: 6px;
  padding: 10px;
  font-size: 16px;
}

.page {
  max-width: 1100px;
  margin: auto;
  padding: 16px;
}

.viewer-section {
  grid-template-columns: 1fr;
  gap: 16px;
  display: grid;
}

.viewer {
  background: #fff;
  border: 1px solid #d1d5db;
  border-radius: 8px;
  width: 100%;
  overflow: hidden;
}

.panel-info {
  background: #fff;
  border: 1px solid #d1d5db;
  border-radius: 8px;
  padding: 16px;
  font-size: 16px;
}

.inputs, .cutlist {
  background: #fff;
  border: 1px solid #d1d5db;
  border-radius: 8px;
  margin-top: 24px;
  padding: 16px;
}

.cutlist li {
  padding: 6px 0;
  font-size: 15px;
}

@media (width >= 900px) {
  .viewer-section {
    grid-template-columns: 2fr 1fr;
  }

  .inputs {
    grid-template-columns: repeat(2, 1fr);
    gap: 16px;
    display: grid;
  }

  .inputs label {
    grid-column: span 1;
  }
}

* {
  box-sizing: border-box;
}

body {
  color: #222;
  background: #f5f6f8;
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
}

header {
  color: #fff;
  background: #1f2937;
  align-items: center;
  height: 56px;
  padding: 0 16px;
  font-size: 18px;
  font-weight: bold;
  display: flex;
}

.layout {
  height: calc(100vh - 56px);
  display: flex;
}

aside {
  color: #fff;
  background: #111827;
  width: 220px;
  padding: 16px;
}

aside a {
  color: #e5e7eb;
  margin-bottom: 12px;
  display: block;
}

aside a:hover {
  color: #fff;
}

main {
  flex: 1;
  padding: 32px;
  overflow-y: auto;
}

h1, h2, h3 {
  margin-top: 0;
}

section {
  margin-bottom: 32px;
}

.form {
  grid-template-columns: 1fr;
  gap: 12px;
  max-width: 400px;
  display: grid;
}

label {
  font-weight: bold;
}

input {
  padding: 8px;
  font-size: 14px;
}
